export const Categories = ({ value, onChangeCategory }) => {
  const categories = [
    "",
    "",
    "",
    "",
    "",
    "",
  ];

  return (
    <div className="categories">
      <ul>
        {categories.map((category, i) => (
          <li
            key={i}
            onClick={() => onChangeCategory(i)}
            className={value === i ? "active" : ""}
          >
            {category}
          </li>
        ))}
      </ul>
    </div>
  );
};
